.project {
    display        : flex;
    flex-direction : column;
    flex-wrap      : wrap;
    justify-content: center;
    align-items    : center;
    width          : 80%;
    margin-left    : 10%;
    margin-right   : 10%;
}

img.screenshot {
    width : 37rem;
    height: 24rem;
    border-radius: 20px;
}

.project img:hover {
    transform: scale(1);


}

.title {
    font-size     : 2rem;
    font-weight   : 600;
    padding-bottom: 15px;
}

.description h4 {
    margin-top: 10px;
    text-align: center;
    color     : white;
}

.description {
    display       : flex;
    flex-direction: column;
    flex-wrap     : wrap;
    width         : 94%;
    border-radius : 15px;
    margin-top    : 15px;
    margin-left   : 10px;
    margin-right  : 10px;
    padding-left    : 20px;
    padding-right   : 20px;
    margin-bottom   : 20px;
    align-content   : center;
    background-color: rgba(255, 255, 255, 0.3);

}

.description p {
    font-size: 1rem;
}

.description h4 {
    text-align    : center;
    color         : white;
    margin-top    : 15px;
    font-size     : 1.2rem !important;
    padding-bottom: 5px;
}

.launch {
    font-weight: 600 !important;
}

.project-links,
.project-links ul,
.project-links li,
.project-links abbr {
    display        : inline-flex;
    flex-direction : row;
    justify-content: center;
    width          : inherit;
    padding-left: 25px;
    padding-right: 25px;
}

.project a {
    font-weight: normal;
}

.icons-row {
    display       : flex;
    flex-direction: row;
    margin: 0; 
    padding: 0;
}

div.icons {
    display        : flex;
    flex-direction : row;
    flex-wrap      : wrap;
    height         : 100px;
    justify-content: center;
    align-content  : center;
    margin: 0 !important;
}

div.icons ul li {
    display: flex;
    flex-direction: row;
    /* flex-wrap: nowrap;  */
    margin: 0 !important;
    padding: 0 !important;

}

.icons img {
    height : 50px;
    
}

.details {
    display        : flex;
    flex-direction : row;
    flex-wrap      : wrap;
    width          : 100%;
    justify-content: center;
}

.detail {
    display         : flex;
    flex-direction  : column;
    flex-wrap       : wrap;
    width           : 45%;
    border-radius   : 15px;
    justify-content : flex-start;
    background-color: rgba(255, 255, 255, 0.3);
    margin-top      : 15px;
    margin-left     : 10px;
    margin-right    : 10px;
    padding         : 5px;
    text-align      : left;
}

.back .fas {
    font-size: xxx-large;
}

.detail p {
    padding-left : 7px;
    padding-right: 7px;
    margin-bottom: 100px;
    font-size    : .8rem;
    margin-top   : 0;
}

.description p {
    margin-bottom: 50px;
}

.paragraph {
    display       : flex;
    flex-direction: row;
    flex-wrap     : wrap;
    padding       : 5px;
    width         : 90%;
    border        : solid;
    border-radius : 15px;
}

.discuss {
    width          : 100%;
    display        : flex;
    flex-direction : row;
    justify-content: center;
}

.projectsNav {
    display: flex;
    width  : 50%;
}

@media only screen and (max-width: 800px) {

    .detail {
        width: 98%;
    }
}